"use client";
import "@fortawesome/fontawesome-free/css/all.min.css";
import Link from "next/link";
import { useToken } from "../../context/token_context";
import { useLoginClient } from "../../context/regester/login_context";
import {
  UserCircleIcon,
  ChevronDownIcon,
  ArrowRightOnRectangleIcon,
  LifebuoyIcon,
} from "@heroicons/react/24/outline";
import { useState, useRef, useEffect } from "react";

export default function MinimalNavbar() {
  const { userData, logOut } = useToken();
  const { saveData } = useLoginClient();
  const [dropOpen, setDropOpen] = useState(false);
  const dropRef = useRef(null);

  // حالة للتحميل على الأزرار والروابط
  const [loadingBtn, setLoadingBtn] = useState({
    requests: false,
    projects: false,
    account: false,
    support: false,
    logout: false,
    login: false,
    signup: false,
  });

  const role =
    userData?.["http://schemas.microsoft.com/ws/2008/06/identity/claims/role"] || "";
  const isCustomer = role.toLowerCase() === "customer";
  const isWorker = role.toLowerCase() === "worker";

  useEffect(() => {
    const handleClickOutside = (e) => {
      if (dropRef.current && !dropRef.current.contains(e.target)) {
        setDropOpen(false);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  // دالة لتعيين حالة تحميل مؤقتة (مثلا 2 ثانية)
  const handleLoading = (key) => {
    setLoadingBtn((prev) => ({ ...prev, [key]: true }));
    // ممكن هنا تعمل actual async operation
    setTimeout(() => {
      setLoadingBtn((prev) => ({ ...prev, [key]: false }));
    }, 2000);
  };

  // تعديل دالة logOut لإظهار تحميل أثناء العملية
  const handleLogout = () => {
    setLoadingBtn((prev) => ({ ...prev, logout: true }));
    Promise.resolve(logOut()) // تأكد أن logOut ترجع Promise
      .finally(() => {
        setLoadingBtn((prev) => ({ ...prev, logout: false }));
      });
  };

  return (
    <nav className="relative p-4 text-black shadow-md backdrop-blur-sm z-[9999]">
      <div className="container mx-auto flex justify-between items-center">
        {/* Logo */}
        <Link href="/" className="text-xl font-bold flex items-center">
          <span className="inline-block w-5 h-5 mr-2 rounded-full" />
          logo
        </Link>

        {/* Right section */}
        <div
          className={`lg:flex lg:items-center lg:w-auto w-full lg:block mt-4 lg:mt-0`}
          ref={dropRef}
        >
          {userData ? (
            <>
              <div className="flex gap-4 font-medium px-2">
                <Link
                  href="/requests"
                  className={`hover:text-green-400 px-3 py-1 rounded ${
                    loadingBtn.requests ? "opacity-50 pointer-events-none" : ""
                  }`}
                  onClick={() => handleLoading("requests")}
                >
                  {loadingBtn.requests ?  <i className="fa fa-spinner fa-spin"></i> : "Requests"}
                </Link>
                {!isWorker && (
                  <Link
                    href="/projects"
                    className={`hover:text-green-400 px-3 py-1 rounded ${
                      loadingBtn.projects ? "opacity-50 pointer-events-none" : ""
                    }`}
                    onClick={() => handleLoading("projects")}
                  >
                    {loadingBtn.projects ?  <i className="fa fa-spinner fa-spin"></i> : "Projects"}
                  </Link>
                )}
              </div>

              <div className="relative ml-4">
                <button
                  onClick={() => setDropOpen(!dropOpen)}
                  className="z-[9999] relative flex items-center bg-green-500 text-white px-4 py-2 rounded-md hover:bg-opacity-90 focus:outline-none"
                >
                  <UserCircleIcon className="h-5 w-5" />
                  <span className="ml-2">
                    {saveData.displayName.split(" ")[0]}
                  </span>
                  <ChevronDownIcon
                    className={`h-4 w-4 ml-1 transform transition-transform ${
                      dropOpen ? "rotate-180" : ""
                    }`}
                  />
                </button>

                {dropOpen && (
                  <div className="fixed top-16 right-4 w-40 bg-white border border-gray-200 rounded-lg shadow-lg z-[99999] overflow-hidden text-black">
                    <Link
                      href="/account"
                      className={`flex items-center px-4 py-2 hover:bg-gray-100 ${
                        loadingBtn.account ? "opacity-50 pointer-events-none" : ""
                      }`}
                      onClick={() => handleLoading("account")}
                    >
                      <UserCircleIcon className="h-5 w-5 mr-2 text-gray-600" />{" "}
                      {loadingBtn.account ?  <i className="fa fa-spinner fa-spin"></i> : "Account"}
                    </Link>
                    <Link
                      href="/support"
                      className={`flex items-center px-4 py-2 bg-green-50 hover:bg-green-100 ${
                        loadingBtn.support ? "opacity-50 pointer-events-none" : ""
                      }`}
                      onClick={() => handleLoading("support")}
                    >
                      <LifebuoyIcon className="h-5 w-5 mr-2 text-green-600" />{" "}
                      {loadingBtn.support ?  <i className="fa fa-spinner fa-spin"></i> : "Support"}
                    </Link>
                    <button
                      onClick={handleLogout}
                      disabled={loadingBtn.logout}
                      className={`w-full text-left flex items-center px-4 py-2 hover:bg-gray-100 ${
                        loadingBtn.logout ? "opacity-50 pointer-events-none" : ""
                      }`}
                    >
                      <ArrowRightOnRectangleIcon className="h-5 w-5 mr-2 text-gray-600" />{" "}
                      {loadingBtn.logout ? "Logging out..." : "Log out"}
                    </button>
                  </div>
                )}
              </div>
            </>
          ) : (
            <div className="flex items-center justify-center space-x-4">
              <Link
                href="/login"
                className={`px-4 py-2 border text-white rounded-md hover:bg-gray-100 ${
                  loadingBtn.login ? "opacity-50 pointer-events-none" : ""
                }`}
                onClick={() => handleLoading("login")}
              >
                {loadingBtn.login ?  <i className="fa fa-spinner fa-spin"></i> : "Login"}
              </Link>
              <Link
                href="/signup"
                className={`px-4 py-2 bg-green-500 text-white rounded-md hover:bg-green-600 ${
                  loadingBtn.signup ? "opacity-50 pointer-events-none" : ""
                }`}
                onClick={() => handleLoading("signup")}
              >
                {loadingBtn.signup ?  <i className="fa fa-spinner fa-spin"></i> : "Sign Up"}
              </Link>
            </div>
          )}
        </div>
      </div>
    </nav>
  );
}
